// Variables
$bg-dark: #1a1a1a;
$font-light: #f0f0f0;
$transition-fast: 0.2s;

html {
    scroll-behavior: smooth;
}

// Global styles
body {
    background-color: $bg-dark;
    font-family: sans-serif;
}

// Header
.custom-header {
    min-height: 400px;

    &-bg {
        position: fixed;
        background-image: url(../paper_img/8150248.png);
        width: 100%;
        height: 100%;
        text-align: center;
        overflow: hidden;
        pointer-events: none;

        li,
        button {
            pointer-events: all;
        }

        h1,
        h3 {
            pointer-events: none;
            color: $font-light;
        }

        h1 {
            font-size: 80px;
        }

        h3 {
            font-size: 25px;
            color: rgb(223, 223, 223);
        }
    }
}

// Games Section
.gamesIntro {
    height: 60vh;

    img {
        pointer-events: none;
        height: 100%;
        width: 100%;
        object-fit: cover;
    }

    h1 {
        pointer-events: none;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 80px;
        color: $font-light;
    }
}

.gamesBody {
    display: flex;
    flex-flow: column;
    gap: 20px;

    h1 {
        color: $font-light;
        font-size: 5vh;
        margin: 15px 5px 50px;
    }
}

.gamesItem {
    z-index: 1;
    height: 150px;
    width: 100%;
    border-radius: 15px;
    background-color: rgb(199, 199, 199);
    overflow: hidden;
    transform: translateY(0);
    transition: $transition-fast;
    will-change: transform;

    &:hover {
        transform: scale(1.05);
        box-shadow: -4px 4px 4px -4px rgba(0, 0, 0, 0.5), 0 0 18px rgba(0, 0, 0, 0.8);
    }

    a {
        display: flex;
        gap: 10px;
        height: 100%;
        align-items: center;
        width: 100%;
        transition: $transition-fast;
    }
}

// Items with specific styles
#game01 {
    background: linear-gradient(90deg, rgba(106, 13, 173, 1) 0%, rgba(225, 93, 149, 1) 100%);
    color: $font-light;

    .tag {
        background-color: #d3b8ff;
    }
}

#game02 {
    background: linear-gradient(90deg, rgba(27, 29, 54, 1) 0%, rgba(78, 93, 141, 1) 100%);
    color: $font-light;

    .tag {
        background-color: #A6A3D9;
    }
}

#game03 {
    background: linear-gradient(90deg, rgb(177, 102, 55) 0%, rgb(172, 146, 52) 100%);
    color: $font-light;

    .tag {
        background-color: #FFB347;
    }
}

#software01 {
    background: linear-gradient(90deg, rgb(199, 220, 235) 0%, rgb(221, 223, 226) 100%);
    color: #2C3E50;

    .product-info {
        color: #2C3E50;
    }

    .tag {
        background-color: #00ADEF;
    }
}

#software02 {
    background: linear-gradient(90deg, rgba(30,30,30,1) 0%, rgb(44, 44, 44) 100%);
    color: #2C3E50;

    .tag {
        background-color: #00C4B3;
    }
}

#software03 {
    background: linear-gradient(90deg, rgba(10,15,31,1) 0%, rgb(32, 12, 26) 100%);
    color: #2C3E50;

    .tag {
        background-color: #A855F7;
    }
}

.product-icon {
    $target-height: 135px;

    height: $target-height;
    min-width: calc($target-height * 0.73);
    margin-left: 7px;
    border-radius: 10px;
    overflow: hidden;

    img {
        width: 100%;
        height: 100%;
    }
}

// Game Info
.product-info {
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    padding: 5px;
    color: $font-light;

    p,
    h3 {
        margin: 0;
    }

    h5 {
        margin: 0 0 5px 0;
    }
}

// Contributions
.contributions {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 5px;
    margin: 5px 0px 7px 0px;
}

.tag {
    display: flex;
    max-height: 28px;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
    color: #000000cc;
    border-radius: 20px;
}

// Games layout
.games {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
}

a.fill-div-games {
    display: block;
    height: 100%;
    width: 100%;
    text-decoration: none;
}

.fill-div-games {
    transition: $transition-fast;

    &:hover {
        background-color: rgba(0, 0, 0, 0.5);

        h1 {
            opacity: 1;
            transform: translate(-50%, -50%);
        }

        p {
            opacity: 1;
            transform: translate(-50%, 170%);
        }
    }
}

// About Section
.about-container {
    display: flex;
    gap: 50px;

    &::before, &::after{
        display: none;
    }
}

#skills {
    min-width: 500px;
}

// Section styles
.section-gray-img {
    padding-bottom: 40px;
    overflow: hidden;
    background-image: url(../paper_img/sitetheme2.png);
    background-size: 400px;
    color: rgba(255, 255, 255, 0.81);

    .bg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-image: url(../paper_img/sitetheme2.png);
        background-size: 400px;
        width: 500vw;
        height: 100%;
        z-index: 0;
    }
}

.section-blue-img {
    padding-bottom: 40px;
    overflow: hidden;

    .bg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-image: url(../paper_img/8150248.png);
        background-size: cover;
        background-repeat: no-repeat;
        height: 100%;
        width: calc(100% + 5px);
        z-index: 0;
    }

    p,
    ul,
    h1,
    h2,
    h3 {
        position: relative;
        color: $font-light;
    }
}

// Horizontal Rule
hr {
    border-color: rgba(0, 0, 0, 0.09);
    border-width: 2px;
}

// Specific items
#item02,
#item03,
#item04,
#item06 {
    background-size: cover;
    background-repeat: no-repeat;
}

#item02 {
    background-image: url(../paper_img/dontslip-banner.png);
}

#item03 {
    background-image: url(../paper_img/universalserializer-banner.png);
}

#item04 {
    background-image: url(../paper_img/inimaster-banner.png);
}

#item06 {
    background-image: url(../paper_img/timocomhero-banner2.png);
}

// Media Queries
@media screen and (max-width: 993px) {
    
    .gamesItem {
        height: 233px;
        width: 170px;
    }

    .gamesItem {
        height: auto;
        width: 100%;
    }

    .product-icon {
        align-self: flex-start;
        margin-top: 7px;
    }

    .about-container {
        flex-wrap: wrap;
    }

    #skills {
        min-width: auto;
    }
}